.search-container {
  padding-top: 16px;
  padding-bottom: 16px;
}
.main-container {
  padding-bottom: 75px;
}
.navbar-logo {
  margin: 8px 20px 0 0;
}
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #002440;
  height: 330px;
  color: #e3ebf1;
  font-size: 0.70em;
  padding-top: 60px;
}
.footer a {
  color: #e3ebf1;
}
.borderless > tbody > tr > td {
  border: none;
}
.no-clickonce {
  display: none;
}
.tag {
  background-color: #eff9ff;
  padding: 2px 10px;
  line-height: 2em;
  margin: 0 2px;
}
.banner {
  padding: 10px;
}
.banner.banner-bright {
  background: yellow;
  font-size: 1.25em;
}
.banner.banner-info {
  background: #f4f4f4;
}
.ms-Icon-ul {
  margin-left: 24px;
}
.ms-Icon-ul li {
  position: relative;
}
.ms-Icon-ul li i.ms-Icon {
  position: absolute;
  left: -24px;
}
.jumbotron h1 {
  margin: 32px;
}
.what-is-nuget {
  font-size: 1.15em;
  padding-bottom: 75px;
}
.triangle:after {
  content: "";
  position: absolute;
  bottom: -7px;
  border-width: 7px 7px 0;
  border-style: solid;
  border-color: #fff transparent;
  display: block;
  width: 0;
}
.triangle-left:after {
  left: 15px;
}
.triangle-right:after {
  right: 15px;
}
.circuit-board {
  width: 804px;
  height: 471px;
  position: relative;
  margin: 0 auto;
  background-image: url('../img/circuit-board.png');
  background-repeat: no-repeat;
  background-position: center bottom;
}
.circuit-board .circuit-board-label {
  position: absolute;
  height: 75px;
  background-color: #fff;
  color: #000;
  box-shadow: 0 0 3px black;
}
.circuit-board .circuit-board-label h3,
.circuit-board .circuit-board-label h5 {
  margin: 9px 0 0 0;
}
.circuit-board .circuit-board-pd {
  left: 167px;
  top: 0;
  float: left;
  width: 200px;
}
.circuit-board .circuit-board-pv {
  left: 3px;
  top: 173px;
  float: left;
  width: 144px;
}
.circuit-board .circuit-board-up {
  right: 3px;
  top: 159px;
  float: right;
  width: 144px;
}
.package-details h4 > span {
  border-bottom: 1px solid #d8d8d8;
  margin-bottom: 3px;
}
.package-details .dependency-group li span {
  color: #666;
}
.package-details .no-border {
  border: 0;
}
.package-details #show-hidden-versions:hover,
.package-details #show-hidden-versions:focus {
  text-decoration: none;
}
.package-details .package-details-info .ms-Icon-ul li {
  margin-bottom: 15px;
}
.package-details .install-tabs {
  font-size: 0.8em;
}
.package-details .install-tabs ul {
  margin-left: 0;
}
.package-details .install-tabs .nav-tabs > li {
  margin-left: 5px;
  padding: 0;
}
.package-details .install-tabs .nav-tabs > li > a {
  color: #000;
  background-color: #eaeaea;
  border: 0;
  padding: 5px 10px;
  margin: 0;
}
.package-details .install-tabs .nav-tabs > li.active > a {
  background-color: #002440;
  border: 0;
  color: #fff;
}
.package-details .install-tabs .tab-pane {
  display: table;
}
.package-details .install-tabs .tab-pane .install-script {
  padding: 8px 10px;
  display: table-cell;
  width: 100%;
  background-color: #002440;
  font-family: Consolas, Menlo, Monaco, "Courier New", monospace;
  color: #fff;
}
.package-details .install-tabs .tab-pane .install-script span::before {
  content: "PM> ";
}
.package-details .install-tabs .tab-pane .copy-button {
  display: table-cell;
}
